import { StyledText } from '@/components/PageComponents/style'

export default {
  name: 'RoyText',
  props: {
    element: {
      type: Object,
      default: () => {}
    },
    propValue: {
      type: String,
      default: ''
    }
  },
  render() {
    const style = this.element.style || {}
    return (
      <StyledText props={style}>
        <div domPropsInnerHTML={this.propValue}></div>
      </StyledText>
    )
  }
}
